/* reset */
body,
p,
a,
ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
li {
  list-style-type: none;
}

/* mobile styles  */
.grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}
.discriptionBlock {
  max-width: 60%;
  margin: 20px 20%;
}
.discriptionBlock .avatar {
  grid-column: 4/5;
  border-radius: 1px;
  box-shadow: 0 0 2px 1px;
}
.discriptionBlock input[type="file"] {
  display: none;
}

.discriptionBlock .choosePhotoButton {
  margin-top: 10px;
  text-align: center;
  width: 80px;
  grid-column: 4/6;
  background-color: rgb(15, 193, 158);
  font-size: 0.6em;
  padding: 2px 7px;
  color: aliceblue;
  cursor: pointer;
  border: solid 1px grey;
}
.discriptionBlock .aboutInfo {
  text-align: center;
  grid-column: 4/5;
}
.discriptionBlock .fullName {
  color: black;
  font-weight: bolder;
  margin: 0;
  font-size: 1.3em;
}

/* User information setion */
.informAboutUserWrapper .sectionHeader {
  display: flex;
  justify-content: center;
}
.informAboutUserWrapper .sectionHeader img {
  padding-right: 15px;

  height: 25px;
  align-self: center;
}
.informAboutUserWrapper .sectionHeader h3 {
  font-size: 1.1em;

  display: inline;
}

.profileInfoBlock {
  margin: 0 auto;
  width: 380px;
}
.profileInfoBlock .profileInfoRow {
  padding-bottom: 8px;
}
.profileInfoBlock .profileInfoContactsBlock {
  padding-left: 10px;
}
.profileInfoBlock .buttonEdit button {
  margin-top: 10px;
  background-color: rgb(49, 49, 49);
  font-size: 0.8em;
  padding: 2px;
  width: 85px;
  color: aliceblue;
  cursor: pointer;
}
.profileInfoBlock .buttonEdit {
  text-align: center;
}

/* small tablet styles */
@media screen and (min-width: 620px) {

  .informAboutUserWrapper .sectionHeader {
    margin-right: 4%;
  }
 
  .profileInfoBlock .profileInfoRow {
    padding-bottom: 15px;
  }
  .profileInfoBlock .profileInfoContactsBlock {
    padding: 5px 0 0 15px;
  }
  .profileInfoBlock .buttonEdit button {
    background-color: rgb(49, 49, 49);
    padding: 3px;
    width: 100px;
  }
  .discriptionBlock .choosePhotoButton {
    margin-top: 15px;
    width: 100px;
    padding: 2px 7px;
  }
 

}
