/* reset */
body,
p,
a,
ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
li {
  list-style-type: none;
}

/* mobile styles  */
.loginBlock {
  display: flex;
  margin: 10px 10px 0 0;
  align-self: center;
}
.loginBlock p {
  margin: 0;
  display: none;
}
.avatar {
  border-radius: 50%;
  border: solid 1px rgb(67, 67, 67);
  width: 60px;

}
.logoutButton {
  align-self: center;
  margin-left: 10px;
  background-color: rgb(15, 193, 158);
  width: 60px;
  font-size: 12px;
  padding: 2px 7px;
  color: rgb(230, 230, 230);
  cursor: pointer;
  border: solid 1px rgb(11, 151, 123);
}

/* small tablet styles */
@media screen and (min-width: 620px) {
  .avatar{
    width:80px
  }
}

/* large tablets & laptop styles */
@media screen and (min-width: 960px) {
  .loginBlock p {
    display: inline;
    font-weight: 600;
  }
  .loginBlock{
    display: flex;
    text-align: center;
    margin: 20px 30px;
  }
  .loginBlock * {
    align-self: center;
    margin-left: 15px;

  }


}
