/* reset */
body,
p,
a,
ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
li {
  list-style-type: none;
}


/* mobile styles  */
.userBlock {
  background-color: rgb(232, 232, 232);
  border: solid 1px rgb(201, 201, 201);
  margin: 20px auto;
  width: 200px;
  text-align: center;
  padding: 20px 0 20px;
}

.userBlock img {
  border-radius: 50%;
  width: 100px;
  border: solid 1px rgb(201, 201, 201);

}
.userName{
  font-weight: bold;
  color: rgb(49, 49, 49);
  margin: 10px;
}
.userBlock button {
  background-color: rgb(15, 193, 158);
  width: 80px;
  font-size: 16px;
  padding: 2px 7px;
  color: rgb(230, 230, 230);
  cursor: pointer;
  border: solid 1px rgb(11, 151, 123);

}
