/* reset */
body,
p,
a,
ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
li {
  list-style-type: none;
}
/* mobile styles  */
.message {
  font-size: 0.9em;
  padding: 10px;
  width: fit-content;
  background-color: rgb(15, 193, 158);
  border: solid 2px rgb(13, 158, 129);
  border-radius: 15px 15px 0;
  color: rgb(38, 38, 38);
  margin: 5px;
  font-style: italic;
  max-width: 200px;
}
.AddMessagesForm {
  margin: 20px 8px;
}
.addMessageBlock {
  padding: 15px 50px;
  background-color: rgb(235, 235, 235);
  border: solid 2px rgb(220, 220, 220);
}

.addMessageBlock button {
  background-color: rgb(49, 49, 49);
  height: auto;
  font-size: 15px;
  padding: 3px 10px;
  width: auto;
  color: aliceblue;
  cursor: pointer;
  transition: all 0.3s linear;
}

/* small tablet styles */
@media screen and (min-width: 620px) {
  .AddMessagesForm {
    margin: 20px 50px;
  }
  .message {
    font-size: 0.9em;
    width: fit-content;
    margin: 10px;
    font-style: italic;
    max-width: 230px;
  }

}
